<template>
      <div class="main-panel" >
       
        <div class="row" style="margin-top:75px;margin-left:30px" >
          <div class="col-md-2"></div>
          <div class="col-md-10">
            <div class="card">
              <div class="card-header">
                <h5 class="title">Kategori Ekle</h5>
              </div>
              <div class="card-body">
                <form>
                  <div class="row">
                    <div class="col-md-5 pr-md-1">
                      <div class="form-group">
                        <label>Şirket </label>
                        <input type="text" class="form-control" disabled="" placeholder="Zeki Usta" >
                      </div>
                    </div>
                    <div class="col-md-3 px-md-1">
                      <div class="form-group">
                        <label>Kategori Adı</label>
                        <input type="text" class="form-control" placeholder="Kategori Adı" v-model="category.name" >
                      </div>
                    </div>
                    <div class="col-md-4 pl-md-1">
                      <div class="form-group">
                        <label for="exampleInputEmail1">Kategori Resmi </label>
                        <input type="number" class="form-control" placeholder="Resim" v-model="category.image">
                      </div>
                    </div>
                    
                  </div>
                 
                 
                 
                 
                </form>
              </div>
              <div class="card-footer text">
                <button @click="addProduct" type="submit" class="btn btn-fill btn-primary">Kaydet</button>
              
              </div>
            </div>
          </div>
       
        </div>
         <div class="row">
           <div class="col-md-2"></div>
            <div class="col-md-9"  style="margin-left:20px">
            <div class="card card-user">
             <div class="card-body">
                <div class="table-responsive" style="overflow:auto">
                  <table class="table tablesorter " id="">
                    <thead class=" text-primary">
                      <tr>
                        <th>
                        Kategori Adı
                        </th>
                        <th>
                          Dosya Yolu
                        </th>
                        
                      </tr>
                    </thead>
                    <tbody>
                      <tr  >
                        <td>
                       Soğuklar
                        
                
                        </td>
                        <td>
                        assets/resim.jpg

                        </td>
                      
                        <td class="text-center">
                         <button  @click="deleteProduct(product.id)" class="btn btn-fill btn-info">Sil</button>
                        </td>
                      </tr>
                   
                    </tbody>
                  </table>
                </div>
              </div>
             
            </div>
          </div>
        </div>
      </div>
 
</template>

<script>
import swal from 'sweetalert'
import axios from 'axios'
export default {
  data(){
    return{
         
    category:{
        name:'',
        image:'',
    }


    }
  },
  methods:{

    deleteProduct(id){
       const url = 'admin/api/product/del/'+id;

       axios.get(url)
    },

    addProduct(){
      const url = 'admin/api/newCategory'
      if(this.category.name != '' && this.category.img ){
        
        axios.post(url,this.category)
      .then((res) =>{
        console.log(res);
        
           if(res.data.status == "ok"){
                 swal("Kategori Başarıyla Eklendi!", "", "success", {
                button: "Devam Et!",
                timer:1500
      }).then(() =>{
        location.reload();
      })
              
                
             }
          
      })

      }
      else{

      swal("Ürün Eklenemedi!", "", "warning", {
        button: "Devam Et!",
        timer:1500
      });
      }
     
      
    },
    deleteProduct(id){
      const url = 'admin/api/product/del/'+id;
      
      axios.get(url)
      .then((res) => {
          console.log(res);

           if(res.data.status == "ok"){
                 swal("Ürün Başarıyla Silindi!", "", "success", {
                button: "Devam Et!",
                timer:1500
      }).then(() =>{
        location.reload();
      })
              
                
             }else{
                swal("Ürün Silinemedi!", "", "warning", {
        button: "Devam Et!",
        timer:1500
      });

             }
          
      })

    }
  

   

  },
  created(){
    const url = 'admin/api/allCateogry'
    axios.get(url)
    .then((response) =>{
    
    
        this.categoryList = response.data;
       
  

    });

    const url2 = 'admin/api/allproduct'
    axios.get(url2)
    .then((res) =>{
    
          
        this.productList = res.data;
          
          
        
      
      

    });




  }


}
</script>

